import { screen } from "env";
import styled from "styled-components";
import { PaddedLayout } from "components/layouts/PaddedLayout";
import { TableCell } from "@mui/material";

export const Container = styled(PaddedLayout)`

  .whiteBlock1 {
    text-align: center;
    background: white;
    border-radius: 15px;
    padding: 15px;
    position:relative;
    transition: background-color 0.2s ease-in-out; 
    h6{
      color:#ababab;
    },
    h4{
        font-weight: 800;
        font-size: 2.325rem;
    }
    .NFTs-link {
      position: absolute;
      width: 18px;
      right: 10px;
      top: 10px;
      color: #b5b5b5;
    }
  }
  .whiteBlock2 {
    background: white;
    border-radius: 15px;
    padding: 15px;
    height: 40vh;
  }

  button {
    text-transform: none !important;
  }

  @media (max-width: ${screen.mobile.max}px) {
    
  }

  // pc
  @media (min-width: ${screen.monitor.min}px) {
  }

  // under pc
  @media (max-width: ${screen.pc.max}px) {
  }

  // mobile
  @media (max-width: ${screen.mobile.max}px) {
  }
`;

export const CustomCell = styled(TableCell)(() => ({
  borderBottom: 'none',
  height: '40px',
  fontSize: '20px'
}))
