import { ChangeEvent, useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";

import { Box, Button, Grid, Typography, IconButton, Stack, Accordion, AccordionSummary, AccordionDetails, TextField, InputAdornment, Skeleton } from "@mui/material";
import CloseIcon from '@mui/icons-material/Close';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import DeleteOutlineIcon from '@mui/icons-material/DeleteOutline';
import FileUploadOutlinedIcon from '@mui/icons-material/FileUploadOutlined';

import { Action } from "near-wallet-selector";

import { SERVER_URL } from "config/config";
import { Container } from "./style";
import { ContractInfoType, Royalties } from "type";

import { axiosInstance } from "contexts/utils";
import { useAlert } from "contexts/AlertContext";
import { useSpinner } from "contexts/GlobalSpinner";
import { useWalletSelector } from "contexts/WalletSelectorContext";

import DisconnectedWallet from "components/DisconnectedWallet";

export default function ProjectSetting() {
  const navigate = useNavigate();
  const { showAlert } = useAlert();
  const { showSpinner, hideSpinner } = useSpinner();

  const { selector, accountId, setWalletSignin } = useWalletSelector();

  const urlParams = new URLSearchParams(window.location.search);
  const tokenContract = urlParams.get('contract');


  const [connected, setConnected] = useState(false);

  const [selectedFile, setSelectedFile] = useState<File | null>();
  const [preview, setPreview] = useState<string | null>(null);

  const [royaltyData, setRoyaltyData] = useState<Royalties[]>([]);

  const [tokenData, setTokenData] = useState<ContractInfoType>();
  const [initialTokenData, setInitialTokenData] = useState<ContractInfoType>();
  const [newOwner, setNewOwner] = useState<string>("");

  const gotoView = () => {
    navigate(`/view?contract=${tokenContract}`)
  }

  const handleFileChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const { files } = event.target;
    const selectedFiles = files as FileList;

    if (selectedFiles) {
      setSelectedFile(selectedFiles?.[0]);
      const reader = new FileReader();
      reader.onloadend = () => {
        setPreview(reader.result as string | null);
      };
      reader.readAsDataURL(selectedFiles?.[0]);
    } else {
      setSelectedFile(null);
      setPreview(null);
    }
  };

  const handleUpload = async () => {
    if (selectedFile) {
      const formData = new FormData();
      formData.append('file', selectedFile);

      try {
        await axiosInstance.post(`/contract/upload?contract=${tokenContract}`, formData, {
          headers: {
            'Content-Type': 'multipart/form-data',
          },
        });
      } catch (error) {
        console.error(error);
        showAlert("error", String(error))
      }
    }
  };

  const updateBasicInfo = () => {
    showSpinner()

    axiosInstance.post('/contract/update_basic', tokenData).then(async (res) => {
      await handleUpload();
      showAlert("success", "You have updated basic information.")
      hideSpinner()
    })
  }

  const updateRoyalyInfo = () => {
    showSpinner()

    axiosInstance.post('/contract/update_royalty',
      { royalties: royaltyData, contract: tokenContract }
    ).then((res) => {
      if (res.data.success) {
        showAlert("success", "You have updated royalty information.")
      } else {
        showAlert('error', "Error")
      }
      hideSpinner()
    })
  }

  const transferOwnership = () => {
    if (newOwner.length > 0) {
      showSpinner()
      setWalletSignin(tokenContract as string, "transfer_ownership", async (data) => {
        if (!data) return;
        const wallet = await selector.wallet();
        const action: Action = {
          type: "FunctionCall",
          params: {
            methodName: "transfer_ownership",
            args: {
              new_owner: newOwner
            },
            gas: '200000000000000',
            deposit: `0`,
          }
        };
        wallet
          .signAndSendTransaction({
            actions: [action],
          }).then(() => {
            showAlert('success', "You have changed ownership successfully.")
          })
          .catch((err) => {
            console.log(err)
            showAlert("error", "Failed FunctionCall change ownership");
            throw err;
          });
      });
      hideSpinner()
    } else {
      showAlert('info', "Please input new owner account")
    }
  }

  const handleRemove = () => {
    setSelectedFile(null);
    setPreview(null);
  };

  const addRoyaltyAccount = () => {
    setRoyaltyData((prevRoyalties) => [
      ...prevRoyalties,
      { address: '', fee: 5 },
    ]);
  }

  const handleAddressChange = (e: ChangeEvent<HTMLInputElement>, index: number) => {
    setRoyaltyData((prevRoyalties) => {
      const updatedRoyalties = [...prevRoyalties];
      updatedRoyalties[index] = {
        ...updatedRoyalties[index],
        address: e.target.value,
      };
      return updatedRoyalties;
    });
  };

  const handleNewOwnerChange = (e: ChangeEvent<HTMLInputElement>) => {
    setNewOwner(e.target.value);
  }

  const handleFeeChange = (e: ChangeEvent<HTMLInputElement>, index: number) => {
    setRoyaltyData((prevRoyalties) => {
      const updatedRoyalties = [...prevRoyalties];
      updatedRoyalties[index] = {
        ...updatedRoyalties[index],
        fee: Number(e.target.value),
      };
      return updatedRoyalties;
    });
  };

  const handleRemoveRoyalty = (index: number) => {
    setRoyaltyData((prevRoyalties) => {
      const updatedRoyalties = [...prevRoyalties];
      updatedRoyalties.splice(index, 1);
      return updatedRoyalties;
    });
  };

  const handleOnchange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>,
    field: keyof ContractInfoType
  ) => {
    setTokenData((prevState) => ({
      ...prevState!,
      [field]: e.target.value,
    } as ContractInfoType));
  };


  const init = async () => {
    const connected = selector.isSignedIn();
    setConnected(connected);
    if (connected) {
      showSpinner()
      axiosInstance.get(`/contract/get_token_by_contract?contract=${tokenContract}`).then(({ data }) => {
        setTokenData(data.token)
        setInitialTokenData(data.token)
        if (data.token.royalties) {
          const _royaltyData = JSON.parse(data.token.royalties);
          _royaltyData.length === 0 ? addRoyaltyAccount() : setRoyaltyData(_royaltyData);
        }
        setPreview(data.token?.icon ? `${SERVER_URL}/${data.token?.icon}` : null)
        hideSpinner()
      }).catch((err) => {
        hideSpinner()
        console.log('err', err)
      })
    }
  };

  useEffect(() => {
    init();
    //eslint-disable-next-line
  }, [accountId]);


  return (
    <Container >
      {!connected && <DisconnectedWallet />}
      {!connected &&
        <Grid container spacing={2} sx={{ maxWidth: '900px', margin: 'auto' }}>
          <Grid item xs={12}>
            <Skeleton variant="text" />
          </Grid>
          <Grid item md={3} sm={4} xs={6}>
            <Skeleton height={200} />
          </Grid>
          <Grid item md={9} sm={8} xs={12} container >
            <Grid item xs={12}>
              <Skeleton height={30} />
            </Grid>
            <Grid item xs={12}>
              <Skeleton height={80} />
            </Grid>
          </Grid>
          <Grid item xs={12} sx={{ mt: 5 }}>
            <Skeleton variant="text" />
          </Grid>
          <Grid item md={3} sm={4} xs={6}>
            <Skeleton height={200} />
          </Grid>
          <Grid item md={9} sm={8} xs={12} container >
            <Grid item xs={12}>
              <Skeleton height={30} />
            </Grid>
            <Grid item xs={12}>
              <Skeleton height={80} />
            </Grid>
          </Grid>
        </Grid>
      }
      {
        connected && (
          <Grid sx={{ maxWidth: '900px', margin: 'auto' }}>
            <Box sx={{ mb: 4, textAlign: 'center', position: 'relative' }}  >
              <IconButton onClick={gotoView} sx={{ position: 'absolute', right: '0px', top: '-20px' }}>
                <CloseIcon sx={{ width: '18px', height: '18px' }} />
              </IconButton>
              <Typography variant="h5" className="subtitle">
                {`Setting (${initialTokenData?.name ?? "contract"})`}
              </Typography>
            </Box>
            <Accordion defaultExpanded >
              <AccordionSummary
                expandIcon={<ExpandMoreIcon />}
                aria-controls="panel1-content"
                id="panel1-header"
              >
                <Typography variant="h5">Basic Information</Typography>
              </AccordionSummary>
              <AccordionDetails>
                <Stack gap={4} justifyContent={'center'} sx={{ flexDirection: { sm: 'row', xs: 'column' } }}>
                  <Box sx={{ margin: 'auto' }}>
                    <Box component='label' htmlFor="file-input">
                      <Box
                        component='input'
                        id="file-input"
                        type="file"
                        accept="image/*"
                        style={{ display: 'none' }}
                        onChange={handleFileChange}
                      />
                      <Box
                        border={1}
                        borderColor="#253b806e"
                        borderRadius={2}
                        display="flex"
                        justifyContent="center"
                        alignItems="center"
                        width={150}
                        height={190}
                        bgcolor={preview ? 'transparent' : 'white'}
                        sx={{
                          overflow: 'hidden',
                          transition: 'background-color 0.2s ease-in-out',
                          '&:hover': {
                            cursor: 'pointer',
                            borderColor: '#253b80',
                            background: '#adb9df !important',
                            transition: 'background-color 0.2s ease-in-out',
                            '.image-part': {
                              color: '#253b80'
                            }
                          },
                        }}
                      >
                        {preview ? (
                          <img src={preview} alt="Preview" style={{ width: 150, height: 190 }} />
                        ) : (
                          <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center', position: 'relative' }}>
                            <FileUploadOutlinedIcon className="image-part" sx={{ width: 40, height: 40, color: '#253b80' }} />
                            <Typography className="image-part body_font" variant="body1" sx={{ fontWeight: 700 }}>
                              Upload Image
                            </Typography>
                            <Typography className="image-part body_font" variant="caption" sx={{ position: 'absolute', bottom: '-70%' }}>
                              Max size is 10M
                            </Typography>
                          </Box>
                        )}
                      </Box>
                    </Box>
                    {selectedFile &&
                      <IconButton color="error" aria-label="add to shopping cart" onClick={handleRemove}>
                        <DeleteOutlineIcon />
                      </IconButton>
                    }
                  </Box>
                  <Box sx={{ width: '100%', display: 'flex', flexDirection: 'column', gap: 2 }}>
                    <TextField
                      size="small"
                      id="outlined-basic"
                      label="Contract Name"
                      variant="outlined"
                      placeholder="Contract Name"
                      helperText="Required"
                      value={tokenData?.name || ""}
                      onChange={(e) => handleOnchange(e, 'name')}
                    />
                    <TextField
                      id="outlined-multiline-static"
                      label="Description"
                      multiline
                      rows={2}
                      defaultValue="Default Value"
                      size="small"
                      value={tokenData?.description || ""}
                      onChange={(e) => handleOnchange(e, 'description')}
                    />
                    <TextField
                      size="small"
                      id="outlined-basic"
                      label="Name of the author's book"
                      variant="outlined"
                      placeholder="Name of the author's book"
                      value={tokenData?.bookname || ""}
                      onChange={(e) => handleOnchange(e, 'bookname')}
                    />
                    <TextField
                      id="outlined-multiline-static"
                      label="About us"
                      multiline
                      rows={2}
                      defaultValue="About us"
                      size="small"
                      value={tokenData?.aboutus || ""}
                      onChange={(e) => handleOnchange(e, 'aboutus')}
                    />
                  </Box>
                </Stack>
                <Box sx={{ mt: 3, mb: 1, textAlign: 'center' }}>
                  <Button variant="outlined" sx={{ height: '45px' }} onClick={updateBasicInfo}>
                    Update Basic Info
                  </Button>
                </Box>
              </AccordionDetails>
            </Accordion>
            <Accordion defaultExpanded >
              <AccordionSummary
                expandIcon={<ExpandMoreIcon />}
                aria-controls="panel2-content"
                id="panel2-header"
              >
                <Typography variant="h5">Royalties</Typography>
              </AccordionSummary>
              <AccordionDetails>
                <Grid container spacing={2}>
                  <Grid item xs={12}>
                    <Typography variant="subtitle2" className="body_font">
                      Add NEAR Accounts to split royalties when minting
                    </Typography>
                  </Grid>
                  {royaltyData.map((royalty, index) =>
                    <>
                      <Grid item xs={8}>
                        <TextField
                          type="text"
                          size="small"
                          id="outlined-basic"
                          label="Royalties walet"
                          placeholder="Royalties walet"
                          variant="outlined"
                          fullWidth
                          value={royalty.address}
                          onChange={(e: ChangeEvent<HTMLInputElement>) => handleAddressChange(e, index)}
                        />
                      </Grid>
                      <Grid item xs={3}>
                        <TextField
                          type="number"
                          size="small"
                          id="outlined-basic"
                          label="Fee"
                          placeholder="Fee"
                          fullWidth
                          value={royalty.fee}
                          onChange={(e: ChangeEvent<HTMLInputElement>) => handleFeeChange(e, index)}
                          InputProps={{
                            endAdornment: <InputAdornment position="end">%</InputAdornment>,
                          }}
                        />
                      </Grid>
                      <Grid item xs={1}>
                        <IconButton color="error" aria-label="add to shopping cart" onClick={() => handleRemoveRoyalty(index)}>
                          <DeleteOutlineIcon />
                        </IconButton>
                      </Grid>
                    </>
                  )}
                  <Grid item xs={12}>
                    <Button sx={{ height: '45px' }} onClick={addRoyaltyAccount}>
                      Add Another Account
                    </Button>
                  </Grid>
                </Grid>
                <Box sx={{ mt: 3, mb: 1, textAlign: 'center' }}>
                  <Button variant="outlined" sx={{ height: '45px' }} onClick={updateRoyalyInfo}>
                    Update Royalty Info
                  </Button>
                </Box>
              </AccordionDetails>
            </Accordion>
            <Accordion defaultExpanded >
              <AccordionSummary
                expandIcon={<ExpandMoreIcon />}
                aria-controls="panel3-content"
                id="panel3-header"
              >
                <Typography variant="h5" >Transfer Contract Ownership</Typography>
              </AccordionSummary>
              <AccordionDetails>
                <Grid container spacing={2}>
                  <Grid item xs={12}>
                    <Typography variant="subtitle2" component={'span'} className="body_font">
                      {`Transfer your contract ownership to another account. `}
                    </Typography>
                    <Typography variant="subtitle2" component={'span'} sx={{ color: '#ed5a5a' }}>
                      This is a permanent action that will remove your ownership of this contract.
                    </Typography>
                  </Grid>
                  <Grid item xs={12}>
                    <TextField
                      size="small"
                      id="outlined-basic"
                      label="Transfer Account *"
                      placeholder="Enter NEAR account to transfer"
                      variant="outlined"
                      fullWidth
                      value={newOwner}
                      onChange={handleNewOwnerChange}
                    />
                  </Grid>
                </Grid>
                <Box sx={{ mt: 3, mb: 1, textAlign: 'center' }}>
                  <Button variant="outlined" sx={{ height: '45px', width: '100px' }} onClick={transferOwnership}>
                    Sign
                  </Button>
                </Box>
              </AccordionDetails>
            </Accordion>
          </Grid>
        )
      }

    </Container >
  );
}
